.limited {
    max-width: $maxWidth;
    min-width: $maxWidth;

    @media screen and (max-width: $screenWidthSmall) {
        width: $maxWidthSmall;
        min-width: $maxWidthSmall;
        margin: auto; } }

.centered {
    margin: auto;
    text-align: justify; }


.section {
    display: flex;
    position: relative;
    width: 100vw;
    min-height: 25vh;
    justify-content: center;
    align-items: center;

    .content {
        display: block;

        h3 {
            text-align: left; } }

    > * {
        // min-width: 100%
        text-align: center; } }

.big {
    min-height: calc(100vh - #{$bottomOffset} - 1.5vh - #{$footerTopOffset}); }
